.production {
    position: relative;
    font-size: 1rem;
    padding-top: 10em;
    background-image: url(/img/svg/field-bg.svg);
    background-repeat: no-repeat;
    background-position: right 6em top;
    background-size: contain;

    &__box-auto {
        position: relative;
    }

    &__heading {
        position: absolute;
        top: 12em;
        bottom: 0;
        right: 15em;
        margin: auto;
        height: 19em;
        width: 80em;
        z-index: 1;

        .title-block {
            display: inline-block;
            border-radius: 0.5em;
            background: $rc-beige;
            padding: 3.5em 5em;
            margin: auto;

            &__title {
                font-weight: $rc-font-weight-regular;
                font-size: $rc-font-size-h2;
                line-height: 1.38em;
                text-align: left;
                text-transform: uppercase;
                color: $rc-text;
                margin: 0;
            }
        }
    }

    &__slider {
        position: relative;
        transform: translateX(-50em);

        .swiper-container {
            height: 100%;
        }

        .swiper-pagination {
            left: 54em;
            height: 1em;
            width: 62.5%;

            .swiper-pagination-progressbar-fill {
                top: -0.5em;
                background-color: $rc-accent;
            }
        }

        .swiper-navigation {
            position: absolute;
            display: flex;
            right: -18em;
            bottom: 3em;
            z-index: 1;

            .slider-arrow-right,
            .slider-arrow-left {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 6em;
                height: 6em;
                border-radius: 100em;
                background-color: $rc-dark-green;
                cursor: pointer;

                &:first-child {
                    background-color: $rc-green;
                    margin-right: 2em;

                    .icon {
                        transform: rotate(180deg);
                    }
                }

                .icon {
                    font-size: 2rem;

                    .svg-sprite-icon {
                        fill: transparent;
                        stroke: $rc-dark-beige;
                    }
                }
            }
        }


        .video {
            position: relative;
            padding: 1.5em;
            margin-right: 3em;

            &:nth-child(odd) {

                .video__link {

                    &:after {
                        background-color: $rc-green;
                    }
                }
            }

            &__link {
                position: relative;
                display: block;
                width: 100%;
                height: 100%;

                &:before {
                    content: "";
                    position: absolute;
                    top: -0.5em;
                    left: -0.5em;
                    width: 100%;
                    height: 100%;
                    background-color: $rc-white;
                    opacity: 0.8;
                    z-index: -1;
                }

                &:after {
                    content: "";
                    position: absolute;
                    top: -1em;
                    left: -1em;
                    width: 100%;
                    height: 100%;
                    background-color: $rc-dark-beige;
                    z-index: -2;
                }
            }

            &__poster {
                height: auto;
                width: 100%;
            }

            &__play {
                position: absolute;
                width: 9em;
                height: 9em;
                top: 0;
                bottom: 0;
                right: 0;
                left: -1em;
                margin: auto;
                border-radius: 100em;
                background-color: $rc-white;
                box-shadow: 0 0 0 rgba(255,255,255, 0.8);
                animation: pulse 1.5s infinite;
                opacity: 0.8;
                z-index: 1;

                &:before {
                    content: "";
                    position: absolute;
                    width: 13.7em;
                    height: 13.7em;
                    top: -0.1em;
                    bottom: 0;
                    right: 0;
                    left: -2.3em;
                    margin: auto;
                    border-radius: 100em;
                    background-color: $rc-white;
                    box-shadow: 0 0 0 rgba(255,255,255, 1);
                    animation: pulse 1s infinite;
                    opacity: 0.3;
                    z-index: -1;
                }

                &:after {
                    content: "";
                    position: absolute;
                    width: 18.2em;
                    height: 18.2em;
                    top: 0;
                    bottom: 0;
                    right: 0;
                    left: -4.7em;
                    margin: auto;
                    border-radius: 100em;
                    background-color: $rc-white;
                    box-shadow: 0 0 0 rgba(255,255,255, 1);
                    animation: pulse 2s infinite;
                    opacity: 0.2;
                    z-index: -1;
                }

                .icon {
                    position: absolute;
                    width: 2.2em;
                    height: 1.4em;
                    top: 0;
                    bottom: 0;
                    right: 0;
                    left: 0;
                    margin: auto;
                    transform: rotate(-90deg);
                    z-index: 2;

                    .svg-sprite-icon {
                        fill: $rc-accent;
                    }
                }
            }
        }
    }

}

@-webkit-keyframes pulse {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(255,255,255, 0.4);
    }
    70% {
        -webkit-box-shadow: 0 0 0 2em rgba(255,255,255, 0);
    }
    100% {
        -webkit-box-shadow: 0 0 0 0 rgba(255,255,255, 0);
    }
  }
  @keyframes pulse {
    0% {
      -moz-box-shadow: 0 0 0 0 rgba(255,255,255, 0.4);
      box-shadow: 0 0 0 0 rgba(255,255,255, 0.4);
    }
    70% {
        -moz-box-shadow: 0 0 0 2em rgba(255,255,255, 0);
        box-shadow: 0 0 0 2em rgba(255,255,255, 0);
    }
    100% {
        -moz-box-shadow: 0 0 0 0 rgba(255,255,255, 0);
        box-shadow: 0 0 0 0 rgba(255,255,255, 0);
    }
  }
