.services-agronomist {
    font-size: 1rem;
    position: relative;
    background-position: center right 13.3em;
    background-repeat: no-repeat;
    height: 46em;
    margin: 25em 0 20em;

    &:before {
        content: "";
        position: absolute;
        top: 5em;
        left: 0;
        height: 36em;
        width: 100%;
        background-color: $rc-beige;
        z-index: -1;
    }

    &:after {
        content: "";
        position: absolute;
        top: -93%;
        right: -2%;
        width: 27em;
        height: 36em;
        background-image: url(/img/svg/sprig-mint.svg);
        z-index: -1;
    }

    &__box {
        display: flex;
        justify-content: flex-end;

        &:after {
            content: " ";
            position: absolute;
            top: -5em;
            right: 0;
            font-size: $rc-font-size-h3;
            color: $rc-white;
            text-transform: uppercase;
            background-color: $rc-dark-red;
            padding: 0.8em 5.5em 0.8em 1.66em;
        }
    }

    .blur-block {
        position: relative;
        bottom: -6em;
        width: 86.5em;
        height: 49em;
        background-image: url(/img/comprehensive-care--blur.jpg);
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        padding: 8em;
        z-index: 1;

        &__title {
            font-size: $rc-font-size-h2;
            font-weight: $rc-font-weight-bold;
            line-height: 1.3;
            color: $rc-beige;
            text-transform: uppercase;
            margin: 0 0 1.38em 0 ;
        }

        &__buttons {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            margin-bottom: 1em;
        }

        &__name {
            font-weight: $rc-font-weight-bold;
            font-size: $rc-font-size-h5;
            line-height: 1.3;
            color: $rc-white;
            text-shadow: 0 0.125em 0.25em rgba(0, 0, 0, 0.16);
            width: 10em;
            margin: 0 3em 0 0;
        }

        &__profession {
            display: block;
            font-weight: $rc-font-weight-regular;
            margin-top: 0.5em;
        }

        &__whatsapp {
            display: flex;
            align-items: flex-start;
            text-decoration: underline;
            font-size: $rc-font-size-h5;
            line-height: 1.5;
            color: $rc-white;
            width: 18em;

            .icon {
                font-size: 5rem;
                width: 1.3em;
                margin-right: 0.25em;

                .svg-sprite-icon {
                    fill: $rc-white;
                }
            }
        }
    }
}
