.quiz {
    font-size: 1rem;
    position: relative;
    margin-bottom: 10em;

    &__heading {
        position: relative;
        height: 19em;
        width: 110em;
        z-index: 1;

        .title-block {
            display: inline-block;
            border-radius: 5px;
            background: $rc-beige;
            padding: 3.5em 5em;
            margin: auto;

            &__title {
                font-weight: $rc-font-weight-regular;
                font-size: $rc-font-size-h2;
                line-height: 1.38em;
                text-align: left;
                text-transform: uppercase;
                color: $rc-text;
                margin: 0;
            }
        }
    }
}
