.about {
    font-size: 1rem;
    position: relative;
    margin: 7.5em 0;

    &__box {
        display: flex;
    }

    .five-year {
        position: relative;

        &__img {
            position: relative;
            max-width: 34em;
            width: 34em;
            height: 34em;
        }

        &__img-abs {
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            margin: auto;
        }
    }

    .article {
        position: relative;
        margin-left: 7em;

        &:after {
            content: "";
            position: absolute;
            top: 0;
            right: -20em;
            width: 35em;
            height: 35em;
            background-image: url(/img/svg/grass-about.svg);
            z-index: -1;
        }

        &__title {
            font-size: $rc-font-size-h2;
            font-weight: $rc-font-weight-bold;
            text-transform: uppercase;
            margin: 0 0 0.55em;
        }

        &__subtitle {
            display: block;
            font-size: $rc-font-size-h4;
            line-height: 1.5;
            width: 70%;
            margin: 0 0 1.25em;
        }

        &__text {
            font-size: $rc-font-size-default;
            line-height: 1.5;
            max-width: 50em;
            margin: 0 0 1.85em;
        }

    }
}
