.footer {
    font-size: 1rem;
    position: relative;
    padding: 7.5em 0 0;

    &:before {
        content: "";
        position: absolute;
        background-color: $rc-beige;
        top: 14em;
        width: 100%;
        height: 80%;
        z-index: -1;
    }

    &__scroll-up {
        font-size: 1rem;
        position: relative;
        display: block;
        top: -1em;
        height: 14em;
        width: 100%;
        background-image: url(/img/svg/wave-rot.svg);
        background-position: top center;
        background-repeat: no-repeat;
        background-size: cover;

        &:before {
            content: "";
            position: absolute;
            width: 15em;
            height: 15em;
            top: -2em;
            bottom: 0;
            right: 0;
            left: 0;
            margin: auto;
            border-radius: 100em;
            background-image: url(/img/nachat.png);
            background-position: 0 0;
            background-repeat: no-repeat;
            background-size: contain;
            -webkit-animation: rotating 5s linear infinite;
            -moz-animation: rotating 5s linear infinite;
            -ms-animation: rotating 5s linear infinite;
            -o-animation: rotating 5s linear infinite;
            animation: rotating 5s linear infinite;
            z-index: -1;
        }

        .icon {
            position: absolute;
            top: -1em;
            bottom: 0;
            right: 0;
            left: 0;
            font-size: 2.8rem;
            margin: auto;
            cursor: pointer;
            transform: rotate(180deg);
            z-index: 1;

            .svg-sprite-icon {
                fill: $rc-accent;
            }

            &:before {
                content: "";
                position: absolute;
                width: 3em;
                height: 3em;
                top: -.25em;
                bottom: 0;
                right: 0;
                left: -1em;
                margin: auto;
                border-radius: 100em;
                background-color: $rc-white;
                box-shadow: 0px -3px 33px $rc-beige;
                opacity: 0.6;
                z-index: -1;
            }

            &:after {
                content: "";
                position: absolute;
                width: 6em;
                height: 6em;
                top: -.25em;
                bottom: 0;
                right: 0;
                left: -2.5em;
                margin: auto;
                border-radius: 100em;
                background-color: transparent;
                border: .1em solid $rc-white;
                box-shadow: 0px 3px 33px $rc-beige;
                z-index: -1;
            }

            &:hover {

                &:before {
                    box-shadow: 0px 3px 20px $rc-dark-beige;
                }
            }
        }
    }

    &__box {
        padding: 0 10em;
    }

    .fields {

        &__heading {

            .title {
                font-weight: $rc-font-weight-bold;
                font-size: $rc-font-size-h2;
                line-height: 1.5;
                text-transform: uppercase;
                color: $rc-dark-green;
                text-align: center;
                margin: 1em 0 1.38em;
            }
        }

        &__address {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 5em;
        }

        &__item {
            font-size: 1rem;
            display: flex;

            &:nth-child(1) {
                margin-right: 10em;
            }

            .icon {
                position: relative;
                top: 0.1em;
                display: inline-block;
                font-size: $rc-font-size-default;
                margin-right: 0.5em;

                .svg-sprite-icon {
                    fill: $rc-accent;
                }
            }
        }

        &__location {
            display: inline-block;
            font-size: $rc-font-size-h5;
            margin: 0 1em 0 0;
        }

        &__info {
            display: inline-block;
            font-size: $rc-font-size-h5;
            font-weight: $rc-font-weight-bold;
        }
    }

    .office {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: 4em;

        &__heading {
            display: flex;
            justify-content: space-between;
            max-width: 100%;
            width: 100%;
            padding-bottom: 4em;
            padding-left: 9em;
        }

        &__title {
            font-weight: $rc-font-weight-bold;
            font-size: $rc-font-size-h2;
            line-height: 1.5;
            text-transform: uppercase;
            color: $rc-dark-green;
            margin: 0;
        }

        &__buttons {

            .button {

                &:first-child {
                    margin-right: 2em;
                }
            }
        }
    }

    .contacts {
        padding-left: 10em;
        width: 30%;

        &:after {
            content: "";
            position: absolute;
            left: 38em;
            bottom: 12em;
            width: 55em;
            height: 55em;
            background-image: url(/img/svg/lawn-mower--lianer.svg);
            background-repeat: no-repeat;
            z-index: -1;
        }

        &__logo {
            display: block;
            margin-bottom: 2em;
        }

        &__img {
            height: 7em;
        }

        &__address {
            font-size: $rc-font-size-h5;
            line-height: 1.5;
            color: $rc-text;
        }

        &__time {
            display: block;
            font-size: $rc-font-size-h5;
            line-height: 1.5;
            color: $rc-text;

             b {
                 margin-left: 0.2em;
             }
        }

        &__phone,
        &__mail {
            display: block;
            font-weight: bold;
            font-size: $rc-font-size-h4;
            line-height: 1;
            text-align: left;
            color: $rc-text;
            text-decoration: none;
            margin: 1em 0;

            .icon {
                font-size: $rc-font-size-default;
                margin-right: 0.5em;
            }
        }

        .button {
            margin-bottom: 4em;
        }
    }

    .map {
        font-size: 1rem;
        position: relative;
        width: 60%;
        display: flex;
        align-items: center;
        justify-content: flex-end;

        &:after {
            content: "";
            position: absolute;
            right: -25em;
            bottom: -5em;
            width: 80em;
            height: 32em;
            background-image: url(/img/grass--long.png);
            background-repeat: no-repeat;
            z-index: -1;
        }

        &__img {
            position: relative;
            display: block;
            height: 40em;
            width: 70em;
            border: 0.5em solid $rc-white;
            //box-shadow: 0 0 1.8em rgba(0, 0, 0, 0.1);
            background-repeat: no-repeat;
            background-position: center center;

            &--small {
                position: absolute;
                width: 27.5em;
                height: 14em;
                bottom: 0;
                right: -1em;
                border: 0.5em solid $rc-white;
                box-shadow: 0 0 1.8em rgba(0, 0, 0, 0.1);
            }
        }
    }

    .copyright {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 8em;
        background-color: $rc-white;
        padding: 3em;


        &__copy,
        &__political {
            font-size: $rc-font-size-h6;
            color: $rc-text;
            margin-right: 7em;

            &:nth-child(2) {
                margin-right: 3em;
            }
        }

        .redcat {

            &__agency-logo {
                display: flex;
                align-items: center;

                #site-by {
                    width: 4.3em;
                    height: 1em;
                    margin-right: 0.5em;
                }

                #logotype__R_Cat--black-red {
                    width: 6em;
                    height: 2em;
                }
            }
        }
    }
}
