@import "../helpers/mixins";
@import "../helpers/variables";

:root {
	--full-screen: calc(var(--vh, 1vh) * 100);
}

html {
    overflow-x: hidden;
    box-sizing: border-box;
    font-family: $rc-font-family-default;
    font-size: calc(100vw / 1920 * 10);

    // @include media-desctop {
    //     font-size: 10px;
    // }
    // @include media-laptop {
    //     font-size: 10px;
    // }
    // @include media-tablet-horizontal {
    //     font-size: 10px;
    // }
    // @include media-tablet {
    //     font-size: 10px;
    // }

    // @include media-mobile {
    //     font-size: .9rem;
    // }
}

body {
    position: relative;
    line-height: 1.33;
    font-weight: 400;
    color: $rc-text;
    background-color: $rc-white;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    min-width: 320px;
    width: 100%;
    margin: 0;
}

img {
    max-width: 100%;
    vertical-align: middle;
}

.box-auto {
    position: static;
    max-width: 160rem;
    //font-size: 1rem;
    padding: 0 3em;
    margin: 0 auto;
}

ul {
    padding: 0;
    margin: 0;

    li {
        list-style-type: none;
    }
}

.icon {
    display: inline-block;
    font-size: 1rem;
    height: 1em;
    width: 1em;
}

.svg-sprite-icon {
    display: inline-block;
    font-size: 1rem;
    height: 100%;
    width: 100%;
    fill: $rc-text;
}
