// Mobile first
$rc-mediaWidthMobile: 479px;
$rc-mediaWidthTablet: 767px;
$rc-mediaWidthTabletHorizontal: 1023px;
$rc-mediaWidthLaptop: 1279px;
$rc-mediaWidthDesctop: 1439px;

// USER VARIABLES SECTION

//-- Color ------

$rc-accent: #bc9769;
$rc-text: #3a3a3a;
$rc-border: #62615D;
$rc-white: #ffffff;
$rc-dark-red: #B84E67;
$rc-dark-green: #4A8466;
$rc-green: #81C1A1;
$rc-beige: #F8F5F0;
$rc-dark-beige: #F8F2E6;

//-- Font size ------

$rc-font-size-default: 1.6rem;

$rc-font-size-h1: 8.4rem;
$rc-font-size-h2: 3.6rem;
$rc-font-size-h3: 2.8rem;
$rc-font-size-h4: 2.4rem;
$rc-font-size-h5: 1.8rem;
$rc-font-size-h6: 1.4rem;

//-- Line height ------

$rc-line-height-h1: 6.4rem;
$rc-line-height-h2: 3.6rem;
$rc-line-height-h3: 2.4rem;
$rc-line-height-h4: 2.2rem;
$rc-line-height-h5: 2rem;
$rc-line-height-h6: 1.5rem;

//-- Font weight ------
$rc-font-weight-think: 100;
$rc-font-weight-light: 300;
$rc-font-weight-regular: 400;
$rc-font-weight-bold: 700;

//-- Font family ------

$rc-system: -apple-system, blinkmacsystemfont, arial, sans-serif; // System Font Family
$rc-font-family-default: gothampro, sans-serif; // GothamPro Font Family
$rc-font-family-decor: calligraphtygra, sans-serif; // CalligraphTYGRA Font Family
$rc-font-family-decor-bg: antonellascriptx, sans-serif; // CalligraphTYGRA Font Family
$rc-font-family-currency: candara, sans-serif; // Candara Font Family
$rc-font-family-price: alata, sans-serif; // Alata Font Family

//-- Radio Check ------

$rc-chbx_size: 1.6rem;
$rc-blue: #c02e3b;
$rc-teal: #009688;
$rc-orange: #ff9800;
$rc-light-grey: #f1f1f1;

$rc-tt: 0.2s;
$rc-tt_toggle: 0.4s;
