.services-sale {
    font-size: 1rem;
    position: relative;
    margin: 10em 0;

    &__title {
        font-size: $rc-font-size-h2;
        text-transform: uppercase;
        width: 70%;
        margin: 0 0 0.83em 0;
    }

    &__img {
        position: relative;
        width: 58em;
        height: 36.5em;
        right: -10em;
        z-index: -1;
    }

    &__content {
        display: flex;

        .card-sale {
            display: flex;
            width: 82em;
            background-image: url(/img/svg/card-long.svg);
            background-repeat: no-repeat;
            background-position: center center;
            background-size: contain;
            padding: 6em 8em;
            z-index: 1;

            &__info {
                width: 37em;
                margin-right: 3em;
            }

            &__title {
                font-weight: $rc-font-weight-bold;
                font-size: $rc-font-size-h4;
                line-height: 1.5;
                text-transform: uppercase;
                color: $rc-dark-green;
                margin: 0;
            }

            &__text {
                font-size: $rc-font-size-h5;
                line-height: 1.5;
                color: $rc-text;
                margin-bottom: 0;
            }

            &__order {
                display: flex;
                flex-direction: column;
            }

            &__price {
                font-family: $rc-font-family-price;
                font-size: 6rem;
                line-height: 1.3;
                color: $rc-dark-red;
            }

            &__before,
            &__currency {
                font-family: $rc-font-family-currency;
                font-size: $rc-font-size-default;
                line-height: 1.3;
                color: $rc-text;
                text-transform: uppercase;
                margin: 0 0.5em;
            }

            &__old-price {
                font-family: $rc-font-family-price;
                font-size: $rc-font-size-h3;
                text-decoration: line-through;
                line-height: 1.3;
                color: $rc-border;
                margin-left: 1.2em;
                margin-bottom: 1em;
            }
        }
    }
}
