.home {
    font-size: 1rem;
    position: relative;
    padding-top: 16em;

    &:before {
        content: "";
        position: absolute;
        background-image: url(/img/home-bg.jpg);
        background-position: 0 0;
        background-repeat: no-repeat;
        background-size: cover;
        top: -15em;
        width: 100%;
        height: 100%;
        z-index: -1;
    }

    &__box-auto {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }

    &__offer {
        position: relative;
        display: block;
        width: 50%;
    }

    .offer {

        &__subtitle {
            font-family: $rc-font-family-decor;
            font-weight: $rc-font-weight-regular;
            font-size: 5rem;
            letter-spacing: 0.01em;
            line-height: 1.64em;
            color: $rc-accent;
            margin: 0;

            &:before {
                content: "-.";
                position: absolute;
                font-family: $rc-font-family-decor-bg;
                font-size: 4.45em;
                color: $rc-white;
                white-space: nowrap;
                top: -0.1em;
                left: -0.55em;
                z-index: -1;
            }
        }

        &__title {
            font-weight: $rc-font-weight-regular;
            font-size: $rc-font-size-h1;
            letter-spacing: 0.01em;
            line-height: 1em;
            color: $rc-text;
            margin: 0 0 0.35em 0;
        }

        &__advantage {
            display: flex;

            .text {
                font-weight: $rc-font-weight-regular;
                font-size: 2.4rem;
                letter-spacing: 0.01em;
                color: $rc-text;
                margin: 0 0 2em 0;

                &:nth-child(odd) {
                    margin-right: 1.66em;
                }
            }
        }

        .button {
            margin-bottom: 20em;
        }
    }

    &__scroll-down {
        font-size: 1rem;
        position: relative;
        display: block;
        top: -2em;
        height: 14em;
        width: 100%;
        background-image: url(/img/svg/wave.svg);
        background-position: bottom center;
        background-repeat: no-repeat;
        background-size: cover;

        &:before {
            content: "";
            position: absolute;
            width: 15em;
            height: 15em;
            top: 4.7em;
            bottom: 0;
            right: 0;
            left: 0;
            margin: auto;
            border-radius: 100em;
            background-image: url(/img/dalshe.png);
            background-position: 0 0;
            background-repeat: no-repeat;
            background-size: contain;
            -webkit-animation: rotating 6s linear infinite;
            -moz-animation: rotating 6s linear infinite;
            -ms-animation: rotating 6s linear infinite;
            -o-animation: rotating 6s linear infinite;
            animation: rotating 6s linear infinite;
            z-index: -1;
        }

        .icon {
            position: absolute;
            top: 1.5em;
            bottom: 0;
            right: 0;
            left: 0;
            font-size: 2.8rem;
            margin: auto;
            cursor: pointer;
            z-index: 1;

            .svg-sprite-icon {
                fill: $rc-accent;
            }

            &:before {
                content: "";
                position: absolute;
                width: 3em;
                height: 3em;
                top: -.25em;
                bottom: 0;
                right: 0;
                left: -1em;
                margin: auto;
                border-radius: 100em;
                background-color: $rc-white;
                box-shadow: 0px 3px 33px $rc-beige;
                opacity: 0.6;
                z-index: -1;
            }

            &:after {
                content: "";
                position: absolute;
                width: 6em;
                height: 6em;
                top: -.25em;
                bottom: 0;
                right: 0;
                left: -2.5em;
                margin: auto;
                border-radius: 100em;
                background-color: transparent;
                border: .1em solid $rc-white;
                box-shadow: 0px 3px 33px $rc-beige;
                z-index: -1;
            }

            &:hover {

                &:before {
                    box-shadow: 0px 3px 20px $rc-dark-beige;
                }
            }
        }
    }


}

@-webkit-keyframes rotating  {
    from {
        -webkit-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(-360deg);
        -o-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}
@keyframes rotating {
    from {
        -ms-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -webkit-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -ms-transform: rotate(-360deg);
        -moz-transform: rotate(-360deg);
        -webkit-transform: rotate(-360deg);
        -o-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}
