.recommendation {
    font-size: 1rem;
    background-color: $rc-beige;
    background-position: top 3em left;
    background-repeat: no-repeat;
    background-size: contain;
    height: 50em;
    padding: 12em 12em 12em 0;
    margin-bottom: 10em;

    &__box {
        display: flex;
        justify-content: flex-end;
    }

    .download-block {
        width: 88.8em;
        height: 29.7em;
        border-radius: 1em;
        background-image: url(/img/lawn-mower--blur.jpg);
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        padding: 3em 0 0 6em;
        z-index: 1;

        &__title {
            font-weight: $rc-font-weight-bold;
            font-size: $rc-font-size-h3;
            letter-spacing: 0.02em;
            line-height: 1.4em;
            color: $rc-white;
            text-transform: uppercase;
            width: 12em;
            margin: 0 0 1.07em;
        }

        &__footnote {
            display: block;
            font-size: 1.2rem;
            color: $rc-white;
            margin: 0.76em 4.16em 3.75em 4.16em;
        }

        .button {
            text-transform: initial;
            font-weight: $rc-font-weight-regular;

            b {
                color: $rc-dark-red;
                margin-left: 0.3em;
            }
        }
    }

}
