.price {
    font-size: 1rem;
    position: relative;
    padding-top: 15em;
    padding-bottom: 10em;

    &__box-auto {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }

    .widget {
        position: relative;
        display: inline-block;
        max-height: 43.5em;
        margin-bottom: 5em;

        &:nth-child(2) {

            &:after {
                content: "";
                position: absolute;
                width: 35em;
                height: 30em;
                top: -15em;
                left: -10em;
                background-image: url(/img/svg/maple.svg);
                background-repeat: no-repeat;
                background-size: cover;
                z-index: -1;
            }
        }

        &:nth-child(3) {

            &:after {
                content: "";
                position: absolute;
                width: 24em;
                height: 24em;
                top: -6em;
                left: -15em;
                background-image: url(/img/svg/maple--round.svg);
                background-repeat: no-repeat;
                background-size: cover;
                z-index: -1;
            }

            &:before {
                content: "";
                position: absolute;
                width: 20em;
                height: 20em;
                bottom: -8em;
                right: -5em;
                transform: rotate(105deg);
                background-image: url(/img/svg/maple--round.svg);
                background-repeat: no-repeat;
                background-size: cover;
                z-index: -1;
            }
        }

        &:nth-child(odd) {
            margin-right: 5em;
        }

        &__card {
            width: 70em;
            height: 43.7em;
        }

        &__card-small {
            position: absolute;
            top: 0.5em;
            right: 0.5em;
            width: 47em;
            z-index: -1;
        }

        &__label {
            position: absolute;
            display: inline-block;
            top: -1.5em;
            left: 8em;
            text-align: center;

            .icon {
                font-size: 11em;

                .svg-sprite-icon {
                    fill: $rc-dark-red;
                }
            }
        }

        &__hit {
            position: absolute;
            top: 1.5em;
            left: 0;
            right: 0;
            margin: auto;
            display: block;
            font-size: 1.2em;
            color: $rc-white;
            text-transform: uppercase;
            font-weight: $rc-font-weight-bold;

            b {
                display: block;
                text-align: center;
                font-size: $rc-font-size-h3;
                font-weight: $rc-font-weight-bold;
            }
        }

        &__info {
            position: absolute;
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            top: 15em;
            left: 8em;
            width: calc(100% - 17em);
            z-index: 2;
        }

        &__heading {
            display: inline-block;
            width: 27em;

            .title {
                font-size: $rc-font-size-h3;
                margin-top: 0;
                margin-bottom: 0.4em;
            }

            .subtitle {
                font-family: $rc-font-family-price;
                font-size: $rc-font-size-h4;
                margin: 0;
            }
        }

        &__price {
            display: inline-block;
            text-align: center;
            font-family: $rc-font-family-price;
            font-size: 7.6rem;
            line-height: 0.85;
            color: $rc-dark-red;
            margin: 0;

            hr {
                margin: 0.1em 0;
            }
        }

        &__currency {
            display: block;
            text-align: center;
            font-family: $rc-font-family-currency;
            font-size: 2.1rem;
            color: $rc-text;
            margin: 0 auto;
        }

        &__buttons {
            position: absolute;
            bottom: 6em;
            left: 8em;
            z-index: 2;

            .button {

                &:nth-child(1) {
                    margin-right: 3em;
                }
            }
        }
    }
}
