.popup {
    position: fixed;
    display: none;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    max-height: 100%;
    overflow: hidden;
    overflow-y: auto;
    padding: 2em 0 0;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 999;

    &__content {
        position: relative;
        margin: 5% auto;
        padding: 4em 6em;
        max-width: 27vw;
        background: $rc-border;
        border-radius: 0.7em;
        box-shadow: 0 0.3em 0.3em 1em rgba(0, 0, 0, 0.15);
        animation-name: animatetop;
        animation-duration: 0.4s;
        transform-origin: 50% 0;
        transition: ease-in-out linear 0.2s;

        @include media-mobile {
            max-width: 100%;
            padding: 4em 5em;
        }
    }

    &__headline {
        position: relative;
        margin-bottom: 1em;

        &--select {
            margin-bottom: 0;
        }
    }

    &__title {
        font-size: $rc-font-size-h3;
        font-weight: 700;
        margin: 0;
    }

    &__close {
        position: absolute;
        right: -5%;
        top: -5%;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 6em;
        height: 6em;
        opacity: 1;
        transition: all 0.2s ease;
        cursor: pointer;

        &::before {
            border-radius: 0.7em;
            background: currentColor;
            content: "";
            display: block;
            width: 3em;
            height: 0.4em;
            transform: translateX(1.5em) rotateZ(-45deg);
        }

        &::after {
            border-radius: 0.7em;
            background: $rc-accent;
            content: "";
            display: block;
            width: 2.2em;
            height: 0.4em;
            transform: translateX(-0.8em) translateY(0.3em) rotateZ(45deg);
        }

        &-dot {
            font-size: 1rem;
            position: absolute;
            display: block;
            left: 0;
            top: 0;
            width: .6em;
            height: .6em;
            border-radius: 10em;
            background-color: $rc-accent;
            transform: translateX(2.1em) translateY(1.8em);
            transition: all 0.2s linear;
        }
    }

    &__text {
        position: relative;
        display: inline-block;
        font-size: 2rem;
        margin: 0.5em 0 0;
    }

    &__select {
        position: relative;
        display: block;
        border: 0;
        font-family: $rc-font-family-default;
        font-size: 2rem;
        font-weight: 700;
        color: $rc-dark-red;
        background: $rc-border;
    }

    &__form {
        text-align: center;

        .field {
            width: 100%;
        }
    }

    &__input {
        width: 100%;
    }

    &__button-elit {
        width: 100%;
        margin-bottom: 1em;

        @include media-mobile {
            width: 100%;
            display: block;
            margin: 1em auto 2em;
        }

    }

    &__policy {
        width: 100%;
        text-align: left;
    }

    &__success {
        position: absolute;
        top: 0;
        right: -5%;
        display: block;
        width: 7em;
        height: 7em;

        @include background("../img/svg/icon-success.svg");
    }
}

.ya-map {

    .popup__content  {
        font-size: 1rem;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        max-width: 70em;
        text-align: center;
        border: 0.5em solid $rc-white;
        border-radius: .2em;
        box-shadow: none;
        padding: 0;
    }
}

.thanks {
    z-index: 1999;
}

.download {
    .popup__content {
        max-width: 45vw;

        @include media-mobile {
            max-width: 100%;
            padding: 4em 5em;
        }
    }

    .popup__icon-pdf {
        position: absolute;
        bottom: 1%;
        right: -1%;
        width: 2em;
        height: 2.2em;

        @include background("../img/svg/icon-pdf.svg");
    }

    .popup__form {
        position: relative;
        text-align: left;
        margin-top: 2em;
        z-index: 2;

        .field {
            font-size: 1rem;
            width: 39em;

            @include media-mobile {
                width: 100%;
            }
        }

        .popup__button-elit {
            font-size: 1rem;
            width: 39em;
            z-index: 2;

            @include media-mobile {
                width: 100%;
            }
        }
    }

    .button-download {
        position: absolute;
        bottom: 20%;
        left: 7%;
        font-size: 1.6rem;
        text-align: left;
        color: $rc-accent;
        z-index: 1;
    }

    .popup__icon-download {
        position: relative;
        display: inline-block;
        width: 1em;
        height: 1.1em;

        @include background("../img/svg/icon-download.svg");

        margin-right: 0.5em;
    }

    .popup__img {
        position: absolute;
        display: block;
        right: 0;
        top: 35%;
        width: 45em;
        height: 35em;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
        z-index: 1;

        @include media-mobile {
            display: none;
        }
    }

    .popup__policy {
        font-size: 1rem;
        width: 39em;

        @include media-mobile {
            width: 100%;
        }
    }

    &__button {
        overflow: hidden;
        opacity: 0;
        z-index: -1 !important;
    }
}

.detailed {
    z-index: 998;

    .popup__content {
        max-width: 75vw;
        padding: 5em 10em;

        @include media-mobile {
            max-width: 100%;
            padding: 4em 2.5em;
        }
    }

    .popup__headline {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        margin-bottom: 3em;

        @include media-mobile {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    .popup__title {
        position: relative;
        font-weight: 900;
        color: $rc-accent;
        text-transform: uppercase;
        margin-right: 0.65em;

        @include media-mobile {
            line-height: 1em;
            margin-right: 0;
            padding-top: 1em;
        }

        .popup__desc {
            position: absolute;
            right: 0;
            top: -30%;
            font-size: 1.6rem;
            font-weight: 500;
            text-transform: initial;
            color: $rc-text;

            @include media-mobile {
                top: -5%;
                right: auto;
                left: 0;
            }
        }
    }

    .popup__subtitle {
        font-size: 4rem;
        line-height: 1.2em;
        font-weight: 300;
        text-transform: uppercase;
        margin: 0;

        @include media-mobile {
            font-size: 3rem;
        }
    }

    .popup__close {
        right: 0;
    }

    .popup__top {
        position: relative;
        display: flex;
        font-size: 1rem;
        width: 100%;
        height: auto;
        background-color: $rc-white;
        padding: 3em;
        margin: 0 auto 2em;

        @include media-mobile {
            flex-direction: column;
            padding: 1em;
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            height: 0.3em;
            width: 100%;
            background-color: $rc-white;
            box-shadow: 0 .1em .2em rgba(0, 0, 0, 0.16);
        }

        &-left {
            text-align: center;
            font-size: 1rem;
            width: 28em;
            height: 17em;
            margin-right: 3em;

            @include media-mobile {
                width: 100%;
                margin-right: 0;
                height: auto;
            }

            .popup__top-sale {
                font-weight: 400;
                font-size: 1.8rem;
                color: $rc-text;

                &--bold {
                    font-size: 2.5rem;
                    font-weight: 900;
                    margin: 0 .2em;
                }
            }

            .popup__top-img {
                position: relative;
                display: block;
                width: 100%;
                height: 100%;
                object-fit: contain;
                border: .1em solid $rc-white;
                box-shadow: 0 .6em 1em rgba(0, 0, 0, 0.16);
                margin-top: 1em;
            }
        }

        &-center {
            font-size: 1rem;
            width: 56em;
            margin-right: 3em;

            @include media-mobile {
                width: 100%;
                margin-right: 0;
            }

            .popup__top-text {
                font-size: 1.8rem;
            }
        }

        &-right {
            font-size: 1rem;

            .popup__price {
                margin-bottom: 3em;
            }
        }
    }

    .popup__left {
        position: relative;
        display: inline-flex;
        font-size: 1rem;
        width: 80em;
        height: 20em;
        border: .1em solid $rc-border;
        background-color: $rc-white;
        margin-right: 2em;

        @include media-mobile {
            width: 100%;
            height: auto;
        }

        .popup__list {
            list-style-type: none;
            padding: 3em;

            .popup__item {
                position: relative;
                font-size: 1.6rem;
                padding-left: 2.5em;
                margin-bottom: 0.8em;

                &::before {
                    content: "";
                    position: absolute;
                    display: block;
                    font-size: 1rem;
                    width: 2.5em;
                    height: 2.1em;
                    top: .1em;
                    left: 0%;

                    @include background("../img/svg/icon-list-style.svg");
                }
            }
        }
    }

    .popup__right {
        position: relative;
        display: inline-flex;
        font-size: 1rem;
        width: 41.5em;
        height: 20em;
        border: .1em solid $rc-border;
        background-color: $rc-white;
        margin-bottom: 3em;

        @include media-mobile {
            width: 100%;
            height: auto;
        }

        .popup__list {
            font-size: 1rem;
            list-style-type: none;
            padding: 2.5em 2em;

            .popup__item {
                position: relative;
                font-size: 1.6rem;
                font-weight: 700;
                padding: 0.4em 0.6em;

                &:nth-child(odd) {
                    background-color: $rc-accent;
                }

                .popup__light {
                    font-weight: 300;
                }
            }
        }
    }

    .popup__form {
        display: flex;

        @include media-mobile {
            flex-direction: column;
        }

        .title {
            font-size: $rc-font-size-h3;
            text-align: left;
            margin: 0 1.2em;

            @include media-mobile {
                font-size: $rc-font-size-h4;
                margin-bottom: 1em;
            }
        }

        .field {
            font-size: 1rem;
            max-width: 29em;
            margin-right: 2em;

            .popup__input {
                max-width: 29em;

                @include media-mobile {
                    max-width: 100%;
                    width: 100%;
                }
            }

            @include media-mobile {
                max-width: 100%;
                width: 100%;
            }
        }

        .field-button {
            width: 35em;

            @include media-mobile {
                width: 100%;
            }

            .popup__button-elit {
                font-size: 1rem;
                width: 35em;

                @include media-mobile {
                    width: 100%;
                }
            }
        }
    }
}

/* Add Animation */

@keyframes animatetop {
    from {
        transform: translateY(100em);
        transform: scale(0);
        opacity: 0;
    }

    to {
        transform: translateY(0);
        transform: scale(1);
        opacity: 1;
    }
}
