.services-park {
    font-size: 1rem;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 46em;
    margin: 20em 0 7.5em;
    z-index: 1;

    &__title {
        position: absolute;
        top: -2.5em;
        right: 0;
        left: 0;
        margin: auto;
        font-size: $rc-font-size-h2;
        text-align: center;
        text-transform: uppercase;
    }

    .blur-block {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        width: 88.8em;
        height: 20em;
        margin-bottom: 2em;
        padding-top: 3em;
        z-index: 1;

        &__title {
            font-size: 3.2rem;
            text-align: center;
            text-transform: uppercase;
            color: $rc-white;
            width: 17em;
        }

        &__subtitle {
            display: block;
            text-transform: lowercase;
            font-weight: $rc-font-weight-regular;
        }

        &__subtitle-link {
            color: $rc-white;
            margin-left: 0.3em;
        }

        .button {
            bottom: -0.5em;
        }
    }
}
