.portfolio {
    font-size: 1rem;
    position: relative;
    margin: 12em 0 7.5em;

    &__box {
        padding-right: 20em;
        padding-left: 20em;
    }

    &__heading {
        display: flex;
        align-items: center;
        margin-bottom: 5em;
    }

    &__title {
        font-size: $rc-font-size-h2;
        color: $rc-dark-green;
        text-transform: uppercase;
        margin: 0 0.83em 0 0;
    }

    &__subtitle {
        font-size: $rc-font-size-h6;
        line-height: 1.3;
        color: $rc-text;
        width: 10em;
    }

    .button {
        margin-right: 0;
        margin-left: auto;
    }

    &__slider {
        font-size: 1rem;

        .slider-default {
            position: relative;
            height: 60em;
            margin-bottom: 2.5em;
            padding-top: 1em;

            .swiper-button-next,
            .swiper-button-prev {
                top: 65%;

                &:after {
                    content: "";
                    width: 2em;
                    height: 3em;
                    background-image: url(/img/svg/arrow-next--big.svg);
                    background-repeat: no-repeat;
                }
            }

            .swiper-pagination {
                position: absolute;
                top: auto;
                left: 0;
                right: 0;
                bottom: 2em;
                height: 1em;
                width: 90%;
                margin: auto;
                background-color: $rc-white;

                .swiper-pagination-progressbar-fill {
                    top: -0.5em;
                    background-color: $rc-green;
                }
            }

            .swiper-button-next {
                right: 5em;
            }

            .swiper-button-prev {
                left: 5em;

                &:after {
                    background-image: url(/img/svg/arrow-prew--big.svg);
                }
            }

            .photo {
                background-repeat: no-repeat;
                background-position: center center;
                background-size: cover;

                &__card {
                    box-sizing: border-box;
                    font-size: 1rem;
                    position: absolute;
                    top: -1em;
                    right: 2em;
                    width: auto;
                    height: auto;
                    border-radius: .3em;
                    background: $rc-dark-beige;
                }

                &__title {
                    font-size: $rc-font-size-h5;
                    line-height: 1.5;
                    color: $rc-text;
                    width: 10em;
                    margin: 1.77em 1.77em 0;
                }

                &__size {
                    display: block;
                    font-size: $rc-font-size-h6;
                    font-weight: $rc-font-weight-bold;
                    color: $rc-dark-red;
                    margin: 0.4em 1.95em 0;
                }

                &__phone {
                    display: block;
                    margin-top: 2.3em;
                    text-align: center;
                    font-size: $rc-font-size-default;
                    color: $rc-text;
                    font-weight: $rc-font-weight-bold;
                    text-decoration: none;
                    margin: 2em auto 0.5em auto;
                }

                &__qustion {
                    display: block;
                    text-align: center;
                    font-size: $rc-font-size-h6;
                    color: $rc-text;
                    margin-bottom: 1em;
                    text-decoration: none;
                }

                &__deadline {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 1rem;
                    width: 100%;
                    height: 7.2em;
                    border-radius: 0 0 0.2em 0.2em;
                    background: $rc-green;
                    color: $rc-white;
                    padding: 1.8em;
                    margin: 0;
                }

                &__date {
                    font-size: $rc-font-size-h6;
                    font-weight: $rc-font-weight-bold;
                    margin: 0.5em;
                }

                .icon {
                    margin-right: 0.5em;
                }

                &__lightbox {
                    position: absolute;
                    left: 2em;
                    top: 2em;
                    color: $rc-white;

                    .icon {
                        font-size: 2.3rem;

                        .svg-sprite-icon {
                            fill: $rc-white;
                        }
                    }
                }

                &__zoom {
                    font-size: $rc-font-size-h6;
                    color: $rc-white;
                }
            }
        }

        .slider-thumb {
            height: 10em;

            .swiper-slide-thumb-active,
            .photo-thumb:hover {

                &:after {
                    content: "";
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    background: $rc-white;
                    opacity: 0.58;
                }

                &:before {
                    content: "";
                    position: absolute;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    left: 0;
                    margin: auto;
                    width: 1.6em;
                    height: 1.6em;
                    background-image: url(/img/svg/plus.svg);
                    background-repeat: no-repeat;
                    z-index: 1;
                }
            }

            .photo-thumb {
                position: relative;
                background-repeat: no-repeat;
                background-position: center center;
                background-size: cover;
            }
        }
    }
}
