// Constants
$rc-button--bg: $rc-accent;
$rc-button--bg-shadow: #8b7160;
$rc-button--color: $rc-white;
$rc-button--fw: $rc-font-weight-bold;
$rc-button--fz: 1.6rem;
$rc-button--fz-sm: 1.2rem;
$rc-button--fz-lg: 1.6rem;
$rc-button--pd: 1.3em 2.15em;


.button {
    position: relative;
    display: inline-block;
    box-sizing: border-box;
    background: $rc-button--bg;
    border: none;
    font-size: $rc-button--fz;
    font-weight: $rc-button--fw;
    color: $rc-button--color;
    text-decoration: none;
    text-align: center;
    padding: $rc-button--pd;
    margin: 0;
    cursor: pointer;
    transition: background 250ms ease-in-out,
                transform 150ms ease;
    transition: .1s ease-in-out;
    -webkit-appearance: none;
    -moz-appearance: none;
    min-width: 16.25em;

    &--small {
        font-size: $rc-button--fz-sm;
    }

    &--large {
        font-size: $rc-button--fz-lg;
    }

    &--rc-custom {
        position: relative;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        text-transform: uppercase;
        transition: all .2s ease-in-out;
        //z-index: 0;

        &:after {
            content: "";
            position: absolute;
            top: 0.4em;
            left: 0.4em;
            background: transparent;
            border: 0.06em solid $rc-accent;
            width: 100%;
            height: 100%;
            transition: all .25s ease-in-out;
            z-index: -1;
        }

        &:before {
            content: "";
            position: absolute;
            display: block;
            top: 0;
            bottom: 0;
            right: -.6em;
            width: 1.4em;
            height: 0.78em;
            background-color: $rc-button--color;
            margin: auto;
            transition: all .25s ease-in-out;
        }

        &:hover {

             &:after {
                top: 0.2em;
                left: 0.2em;
             }

             &:before {
                right: -.4em;
             }
        }

        .icon {
            width: 3.2em;
            height: 2em;
            margin-right: 1.5em;

            .icon-braille {
                fill: $rc-white
            }
        }
    }
}

.button.button-office,
.button-callback,
.button-whatsapp {
    background-color: $rc-dark-beige;
    color: $rc-text;
    border: 0.05em solid $rc-white;
    font-weight: $rc-font-weight-regular;

    &:hover {
        background-color: rgb(238, 227, 207)
    }

    &:after {
        content: none;
    }

    &:before {
        content: none;
    }
}

.button-callback {
    background-color: $rc-white;
    border: 0.05em solid $rc-accent;

    &:hover {
        background-color: $rc-beige;
    }
}

.button-whatsapp {
    background-color: $rc-green;
    border: 0.1em solid $rc-white;
    color: $rc-white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: $rc-font-size-default;

    &:hover {
        background-color: rgb(104, 163, 133) !important;
    }

    .icon {
        font-size: 2.2rem;
        margin-right: 0.5em;

        .svg-sprite-icon {
            fill: $rc-white;
        }
    }

    &:hover {
        background-color: $rc-beige;
    }
}

.button.light {
    background: $rc-dark-beige;
    border: 0.06em solid $rc-button--bg;
    font-weight: $rc-button--fw;
    color: $rc-text;

    &:hover {
        background: $rc-dark-beige;
        color: $rc-button--bg;
    }

    .icon {

        .icon-braille {
            fill: $rc-text;
        }
    }

    &:before {
        background-color: $rc-button--bg;
    }
}

.button:active {

    .icon {

        .icon-braille {
            transform: scale(0.8);
        }
    }
}

/* Let's get this party started */
::-webkit-scrollbar {
    width: 1.2em;
}

/* Track */
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 0.6em rgba(0, 0, 0, 0.3);
    -webkit-border-radius: 0.2em;
    border-radius: 0.2em;
}

/* Handle */
::-webkit-scrollbar-thumb {
    -webkit-border-radius: 0.2em;
    border-radius: 0.2em;
    background: rgba(188, 151, 105, 0.8);
    -webkit-box-shadow: inset 0 0 0.6em rgba(0, 0, 0, 0.5);
}

::-webkit-scrollbar-thumb:window-inactive {
    background: rgba(188, 151, 105, 0.4);
}
