.watering {
    font-size: 1rem;
    position: relative;
    margin: 7.5em 0;

    &__heading {
        display: flex;
        align-items: center;

        .title {
            font-size: $rc-font-size-h2;
            font-weight: $rc-font-weight-bold;
            line-height: 1.5;
            text-transform: uppercase;
            margin: 0;
        }
    }

    &__warranty-label {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        font-size: $rc-font-size-h3;
        font-weight: $rc-font-weight-bold;
        max-width: 15.5em;
        width: 15.7em;
        height: 3.4em;
        border-radius: .07em;
        background: $rc-dark-beige;
        border: .03em solid #bc9769;
        padding: 0.7em;

        .icon {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            font-size: 1em;
            font-weight: $rc-font-weight-bold;
            font-style: normal;
            color: $rc-dark-red;
            width: 1.46em;
            height: 1.46em;
            background: $rc-white;
            border-radius: 100em;
            margin-right: 0.5em;
        }
    }

    &__footnote {
        position: relative;
        top: -0.5em;
        display: flex;
        justify-content: flex-end;
        font-size: $rc-font-size-h6;
        font-weight: $rc-font-weight-regular;
        width: 100%;
    }

    &__items {
        display: flex;
        align-items: flex-end;
        justify-content: space-evenly;
        //flex-wrap: wrap;

        .figure-item {
            font-size: 1rem;
            position: relative;
            display: inline-flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;;
            flex-direction: column;
            min-width: 20em;
            width: 28em;
            height: auto;

            &:nth-child(1) {

                .figure-item__img {
                    max-width: 30em;
                    top: -5em;
                    position: relative;
                }
            }

            &:before {
                content: "";
                position: absolute;
                bottom: 11.2em;
                right: -3.8em;
                left: 0;
                width: 20em;
                height: 20em;
                border-radius: 100em;
                background: $rc-dark-beige;
                box-shadow: 0 0 1em $rc-dark-beige;
                margin: auto;
                z-index: -1;
            }

            &__caption {
                font-size: 2.2rem;
                text-align: center;
                min-width: 13em;
                margin-top: 2.5em;
            }
        }
    }

    &__buttons {
        text-align: center;
        font-size: 1rem;
        margin-top: 5em;
    }

}
