.engineer {
    font-size: 1rem;
    position: relative;
    background-position: center center;
    background-repeat: no-repeat;
    height: 46em;

    &:before {
        content: "";
        position: absolute;
        top: 5em;
        left: 0;
        height: 36em;
        width: 100%;
        background-color: $rc-beige;
        z-index: -1;
    }

    &__box {
        display: flex;
        align-items: flex-end;
        height: 100%;
    }

    .blur-block {
        position: relative;
        bottom: -5em;
        width: 87.8em;
        height: 40em;
        background-image: url(/img/engineer-bg--blur-2.jpg);
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        padding: 8em;
        z-index: 1;

        &__title {
            font-weight: $rc-font-weight-bold;
            font-size: $rc-font-size-h2;
            letter-spacing: 0.02em;
            line-height: 1.4em;
            color: $rc-white;
            text-transform: uppercase;
            margin: 0 0 1.07em;
        }

        .button {
            color: $rc-text;
            border-color: $rc-dark-beige;

            .icon {

                .svg-sprite-icon {
                    fill: $rc-text;
                }
            }

            &:after {
                border-color: $rc-dark-beige;
            }
        }
    }
}
