.header {
    font-size: 1rem;

    &__info-line {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 5em;

        .info-link {

            &__questions {
                margin-right: 3em;
            }

            &__questions,
            &__calculation {
                font-weight: $rc-font-weight-bold;
                font-size: 1.2rem;
                text-transform: uppercase;
                text-decoration: none;
                letter-spacing: 0.01em;
                color: $rc-border;

                &:hover {

                    .icon {
                        transform: scale(1.2);
                        transition: all .5s linear;

                        .svg-sprite-icon {
                            fill: $rc-dark-green;
                            transition: all 0.5s linear;
                        }
                    }
                }
            }

            &__icon {
                transition: all .3s linear;
                margin-right: 0.5em;
            }
        }

        .info-contact {
            font-size: 1rem;

            &__location {
                margin-right: 4em;
            }

            &__location,
            &__mail {
                display: inline-flex;
                align-items: center;
                font-weight: $rc-font-weight-regular;
                font-size: 1.4rem;
                text-decoration: none;
                letter-spacing: 0.01em;
                color: $rc-border;
            }

            &__mail {

                &:hover {
                    text-decoration: underline;
                    color: $rc-dark-green;
                }
            }

            &__icon {
                font-size: 1.6rem;
                margin-right: 0.5em;

                .svg-sprite-icon {
                   fill: $rc-dark-green;
                }
            }
        }
    }

    .n-fixed {
        z-index: 9999;
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        margin: 0;
        padding: 0 10em;
        justify-content: center;
    }

    .mg-fixed {
        margin-bottom: 10em;
    }

    &__navigate {
        font-size: 1rem;
        top:-80px;
        transition: top 1s;
        display: flex;
        align-items: center;
        height: 10em;
        background: $rc-white;
        box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);
        padding: 0 4em;
        margin: 0 2em;

        .logo {
            margin-right: 10em;

            &__img {
                height: 7em;
            }
        }

        .nav-primary {

            &__list {
                display: flex;
            }

            &__item {
                margin-right: 4em;

                &:nth-last-child(1) {
                    margin-right: 7em;
                }
            }

            &__link {
                position: relative;
                font-size: $rc-font-size-default;
                letter-spacing: 0.01em;
                text-align: center;
                color: $rc-text;
                text-decoration: none;

                &::after {
                    position: absolute;
                    content: "";
                    top: 100%;
                    left: 0;
                    width: 100%;
                    height: 3px;
                    background: $rc-green;
                    transform: scaleX(0);
                    transform-origin: right;
                    transition: transform 0.5s;
                }

                &:hover {
                    color: $rc-border;
                }

                &:hover::after {
                    transform: scaleX(1);
                    transform-origin: left;
                }

                &--active {
                    color: $rc-border;

                    &:after {
                        transform: scaleX(1);
                        transform-origin: left;
                    }
                }
            }
        }

        .phone {
            font-weight: $rc-font-weight-bold;
            font-size: $rc-font-size-h5;
            letter-spacing: 0.01em;
            text-align: center;
            color: $rc-dark-green;
            text-decoration: none;
            margin-right: 2.2em;
        }

        .button-call {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 13em;
            padding-left: 3em;

            &:before {
                content: "";
                position: absolute;
                left: 0;
                top: 0;
                bottom: 0;
                width: .1em;
                height: 2.5em;
                background: transparent;
                border: .1em solid #e8e7e3;
                margin: auto;
            }

            &__phone,
            &__whatsapp {
                position: relative;
                width: 4em;
                display: flex;
                align-items: center;
                justify-content: center;
                height: 4em;
                background: radial-gradient(#fff 0%, #f1ece3 100%);
                background-size: 100% 100%;
                padding: .5em;
                margin-right: 1em;
                transform-origin: center;
                transition: all 0.3s linear;
                box-shadow: 0 0 1em rgba(241, 236, 227, 0.6);

                .icon {
                    .svg-sprite-icon {
                        fill: $rc-green;
                    }
                }

                &:hover {
                    width: 3.5em;
                    height: 3.5em;
                    background-size: 50% 50%;

                    .icon {
                        position: absolute;
                        transform: scale(0.9);
                        transition: all 0.3s ease-in-out;
                    }
                }
            }

            &__icon {
                font-size: 2rem;

                .svg-sprite-icon {
                    fill: $rc-green;
                }


            }

            &__toltip {
                position: absolute;
                opacity: 0;
                overflow: hidden;
            }
        }
    }

}
