.packing {
    font-size: 1rem;
    position: relative;
    margin: 15em 0;

    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        height: 100em;
        width: 100%;
        background-color: $rc-beige;
        transform: translateX(50%);
        z-index: -1;
    }

    &__box {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    &__heading {
        position: relative;
        display: block;
        width: 100%;

        .title {
            font-weight: $rc-font-weight-bold;
            font-size: $rc-font-size-h2;
            letter-spacing: 0.02em;
            line-height: 1.4em;
            color: $rc-dark-green;
            text-transform: uppercase;
            text-align: center;
            margin: 1.38em 0;
        }
    }

    .item {
        font-size: 1rem;
        position: relative;
        height: auto;
        width: 52em;
        background-image: url(/img/svg/mint--four.svg);
        background-repeat: no-repeat;
        background-position: top 5em right 0.5em;
        background-color: $rc-white;
        box-shadow: 0px 0px 25px rgba(167, 155, 136, 0.15);
        padding: 5em;
        margin-left: 10em;
        z-index: 1;

        &:nth-child(2) {
            margin-right: 6em;
        }

        .vert-title {
            position: absolute;
            top: 45.2%;
            right: 28.5%;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 82.4em;
            height: 8em;
            background-color: $rc-beige;
            border: 1px solid $rc-accent;
            transform: rotate(-90deg);

            &__title {
                font-size: $rc-font-size-h2;
                color: $rc-dark-green;
                font-weight: $rc-font-weight-regular;
            }
        }

        &:nth-child(2) {

            .button {
                margin-bottom: 2.7em;
            }
        }

        &__card {
            position: relative;
            display: inline-flex;
            flex-direction: column;

            .button {
                display: inline-block;
                max-width: 18.5em;
            }
        }

        &__footnote {
            display: block;
            font-size: $rc-font-size-h6;
            color: $rc-dark-green;
            margin-top: 1.3em;
        }

        &__title {
            display: block;
            font-weight: $rc-font-weight-bold;
            font-size: $rc-font-size-h3;
            line-height: 1.14em;
            color: $rc-text;
            width: 77%;
            margin: 0.35em 0;
        }

        &__price {
            position: absolute;
            right: 0;
            top: 0.9em;
            font-family: $rc-font-family-price;
            font-size: 6em;
            line-height: 1.66em;
            color: $rc-dark-red;
            z-index: 1;
        }

        &__before,
        &__currency {
            font-family: $rc-font-family-currency;
            font-size: $rc-font-size-default;
            line-height: 1.87em;
            color: $rc-border;
            text-transform: uppercase;
        }

        &__subtitle {
            font-size: $rc-font-size-h6;
            color: $rc-text;
            margin: 0 0 1.8em 0;
        }

        &__list {
            position: relative;
            height: 49.5em;
        }

        &__list-title {
            font-weight: $rc-font-weight-bold;
            font-size: $rc-font-size-h5;
            color: $rc-text;

            &:before {
                content: "";
                position: absolute;
                top: 0.7em;
                right: 0;
                width: 12.6em;
                height: 0;
                border: 1px solid $rc-dark-beige;

            }
        }

        .item__item--scroll {
            overflow-y: scroll;
        }

        .item__item--visible {
            opacity: 1;
        }

        .item__item--block {
            transform: translateY(0);
            height: auto;
            margin: 0.83em 0;
            opacity: 1;
            overflow: visible;
        }

        .item__plus-more--minus {

            .icon {

                &:after {
                    content: "";
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    margin: auto;
                    width: 1.2em;
                    height: 0.2em;
                    background-color: $rc-text;
                }

                .svg-sprite-icon {
                    stroke: $rc-dark-beige;
                }
            }
        }

        &__item {
            position: relative;
            display: flex;
            font-size: $rc-font-size-h5;
            color: $rc-text;
            margin: 0.83em 0;

            &--none {
                transform: translateY(-20em);
                height: 0;
                margin: 0;
                opacity: 0;
                overflow: hidden;
                transition: all .5s ease-in-out;
            }

            &--hidden {
                font-size: $rc-font-size-default;
                opacity: 0.25;
                transition: all .3s ease-in-out;

                .icon {
                    margin-right: 0.5em;
                }
            }

            &--plus {
                font-weight: $rc-font-weight-bold;
                cursor: pointer;
            }
        }

        &__plus-more {
            position: absolute;
            top: 0;
            right: 0;

            .icon {
                font-size: 1rem;
                background-color: $rc-dark-beige;
                padding: 0.8em;
                width: 3em;
                height: 3em;

                .svg-sprite-icon {
                    stroke: $rc-text;
                }
            }
        }

        &__icon {
            position: relative;
            font-size: 2.4rem;
            margin-right: 0.9em;

            .icon-guard-check {
                fill: $rc-dark-beige;
            }

            .icon-check {
                position: absolute;
                top: -0.1em;
                left: 0;
                right: -0.1em;
                width: 1.1em;
                fill: transparent;
                stroke: $rc-dark-green;
                stroke-width: 0.2em;
                margin: auto;
            }
        }
    }
}
